.login_form label{
  display: none;
}
.login_form{
  width: 380px;
  margin: 0 auto;
  border: none;
}
.login_form legend{
  font-size: 22px;
  text-align: center;
}
.login_form input{
  width: 350px;
  height: 50px;
  margin: 0 auto;
}
.login_form p{
  width: 100%;
  text-align: center;
}
/*아이디 비밀번호 찾기*/
.login_form a{text-decoration: none; color: #333;}
.login_form p:nth-child(4){
  text-align: right;
  font-size: 14px;
  padding-right: 100px;
}
.login_form p:nth-child(4) >a:nth-child(2){
  padding-right: 20px;
}
/*로그인 회원가입 버튼*/
.login_form p:nth-child(5) > input[type=submit]{
  border: none;
  background: purple;
  font-size: 16px;
  color: #fff;
}
.login_form p:nth-child(6) > input[type=button]{
  border: 1px solid purple;
  background: #fff;
  font-size: 16px;
}

.login_form p:nth-child(7), .login_form p:nth-child(8){
  text-align: right;
  width: 95%;
}