.text01{
  color: #00f;
  font-size: 30px;
  font-weight: bold;
}

.h201{
  width: 90%;
  margin: 0 auto;
  text-align: center;
  margin-bottom: 40px;
}
.h201 img{
  width: 20%;
}

.ul01{
  display: flex;
  width: 90%;
  margin: 0 auto;
  flex-wrap: wrap;
  text-align: center;
  margin-bottom: 40px;
  justify-content: space-around;
}

.ul01 li{
  list-style: none;
  padding: 10px;
  margin: 10px;
  border-radius: 50px;
  background: #efefef;
  text-align: center;

}

.on{
  background: rgb(95, 0, 128) !important;
  color: #fff;
}

.ul02{
  display: flex;
  width: 90%;
  margin: 0 auto;
  flex-wrap: wrap;
  justify-content: space-around;
}
.ul02 li{
  width: 250px;
  list-style: none;
}
.ul02 li >img{
  width: 100%;
  /* height: 200px; */
}
.ul02 li >p{
  text-align: left;
  width: 100%;
  color: #000;
}